.container {
    width: 1200px;
    margin: 0 auto;
}

.footer dl {
    float: left;
    margin: 0 66px;
}

.footer:after {
    content: '';
    display: block;
    clear: both;
}

.footer dt {
    font-size: 20px;
    color: #fff;
    margin-bottom: 54px;
}

.footer dd a,
.footer dd {
    color: #f3f1ed;
}

.footer dd a {
    line-height: 32px;
}

.footer dl:last-of-type dd {
    margin-bottom: 40px;
}

.footer dl:last-of-type dd:after {
    content: '';
    display: block;
    clear: both;
}

.footer span {
    display: block;
    float: left;
    margin-left: 33px;
}

.footer svg {
    float: left;
    display: block;
}


