.story-background {
    background: url()
}

.cn-title-story hr {
    border-color: #fff
}

.cn-title-story h1,
.en-title-story {
    color: #fff;
}

.cn-title-story h1 {
    font-size: 28px;
}

.en-title-story {
    font-size: 14px;
}

.cn-title-story div {
    background-color: #fff;
}

.cn-title-story {
    width: 555px;
    padding-bottom: 0px;
    margin-bottom: 30px;
}

.story .story-describe {
    color: #afaba5;
}

.ancientStory {
    padding: 60px 150px;
    background-color: #f8f6f1;
}

.section01Left {
    padding: 40px 28px 43px 28px;
    background-color: #fff;
    text-align: center;
    margin-right: 180px;
}

.section01Left h2 {
    margin-top: 30px;
    margin-bottom: 10px;
    font-size: 22px;
    color: #333;
}

.section01Left hr {
    width: 25px;
    margin: 0 auto 25px auto;
    border-width: 1.5px;
    border-color: #cc8c51;
}

.ancientStory:after {
    content: '';
    display: block;
    clear: both;
}

.ancientStory div {
    float: left;
}

.section01Right {
    text-align: right;
    margin-top: 122px;
}

.section01Right h2 {
    font-size: 22px;
    margin-bottom: 20px;
    margin-right: 7px;
}

.section01Right hr {
    float: right;
    width: 25px;
    margin-bottom: 50px;
    margin-right: 12px;
}

.section01Right p {
    color: #333;
    line-height: 24px;
    margin-bottom: 38px;
    clear: both;
}

.section01Right a {
    float: right;
    display: block;
    width: 50px;
    padding: 15px 25px;
    background-color: #cc8c31;
    color: #fff;
    border-radius: 15px;
    font-size: 16px;
    margin-right: 12px;
}

.story-product {
    padding: 60px 150px;
    background-color: #afaba5;
}

.story-product div {
    float: left;
}

.story-product hr {
    width: 25px;
    margin: 0 auto;
    border-color: #cc8c51;
}

.story-product:after {
    content: '';
    display: block;
    clear: both;
}

.section03Left {
    padding-top: 155px;
    margin-right: 65px;
    text-align: center;
}

.section03Left>h3 {
    font-size: 22px;
    margin-left: 42px;
    margin-bottom: 104px;
    color: #fff;
    font-size: 22px;
}

.section03Left div h3 {
    margin-top: 21px;
    margin-bottom: 15px;
    font-size: 14px;
}

.section03Left div {
    float: left;
    width: 149px;
    padding: 15px 17px 25px 17px;
    background-color: #fff;
    margin-left: 40px;
}

.section03Left:after {
    content: '';
    display: block;
    clear: both;
}

.section03Right {
    padding: 28px;
    background-color: #fff;
    text-align: center;
}

.section03Right h3 {
    font-size: 22px;
    margin-top: 32px;
    color: #333;
    margin-bottom: 13px;
}

.section03Right hr {
    margin-bottom: 22px;
}

.section03Right p {
    width: 246px;
    font-size: 14px;
    line-height: 21px;
    margin: 0 auto;
}


