.banner {
    position: absolute;
    top: 0;
    width: 100%;
    padding: 12px 0;
    height: 58px;
    background-color: rgba(52, 46, 40, 0.5);
    z-index: 99;
}

.banner:after {
    content: '';
    display: block;
    clear: both;
}

.banner .container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0 150px;
}

.banner img,
.top-nav,
.nav-aside {
    float: left;
}

.top-nav {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 570px;
    text-align: center;
    margin: auto 44px auto 64px;
    line-height: 54px;
}

.top-nav li {
    width: 16.66%;
    float: left;
    -webkit-transition: 0.1s;
    -o-transition: 0.1s;
    transition: 0.1s;
}

.top-nav li a {
    color: #fff;
}

.nav-aside {
    padding-top: 10px;
}

.nav-aside a:last-of-type {
    margin-top: -2px;
    margin-left: 25px;
}

.current-page {
    margin-top: -3px;
    font-size: 20px;
    font-weight: bold;
}

.current-page a {
    color: #f6ca31 !important;
    padding-bottom: 10px;
    border-bottom: 2px solid #f6ca31;
}

.search-bar-active {
    position: relative;
    right: 95px;
}


