.search-bar {
    width: 570px;
    margin: auto 44px auto 64px;
    float: left;
    padding: 8px 0;
}

.search-bar div {
    float: left;
    padding: 10px 40px;
    border-right: 1px solid #fff;
    margin-right: 40px;
}

.search-bar input {
    font-size: 18px;
    padding: 10px 0;
    color: #fff;
    width: 320px;
    background-color: transparent;
    border: 0;
    outline: 0;
}


