.container {
    width: 1200px;
    margin: 0 auto;
}

.shoplist-banner {
    padding: 18px 0 36px 27px;
}

.shoplist-banner>div {
    float: left;
}

.shoplist-banner:after {
    content: '';
    display: block;
    clear: both;
}

.shoplist-banner>div:last-of-type {
    margin-right: 20px;
    margin-left: 358px;
}

.shoplist-banner input {
    float: left;
    width: 479px;
    height: 26px;
    font-size: 22px;
    border: 2px solid #cc8c31;
    padding: 14px 0;
    margin-top: 2px;
}

.shoplist-search-bar {
    position: relative;
    margin-top: 15px;
}

.shoplist-search-bar a {
    position: absolute;
    top: 8px;
    left: 20px;
}

.shoplist-search-bar button {
    float: left;
    background-color: #cc8c31;
    color: #fff;
    font-size: 18px;
    padding: 17px 30px;
    margin-top: 2px;
    border: 0;
    outline: 0;
}

.shoplist-status {
    padding-left: 27px;
    border-bottom: 1px solid #999
}

.shoplist-status:after {
    content: '';
    display: block;
    clear: both;
}

.shoplist-status ul {
    margin: 0;
}

.shoplist-status ul li {
    float: left;
    font-size: 18px;
    padding: 27px 65px;
}

.shoplist-status ul li a {
    color: #333;
}

.shoplist-status ul {
    float: left;
}

.shoplist-status>div,
.shoplist-status a {
    float: right;
}

.shoplist-selected {
    font-weight: bold;
    border-bottom: 3px solid #cc8c31;
    margin-bottom: -2px;
}

.shoplist-selected li a {
    color: #000;
}

.shoplist-status>a {
    display: block;
    padding: 15px 28px;
    border: 1px solid #999;
    border-radius: 12px;
    margin-top: 10px;
}

.shoplist-status div {
    margin-right: 24px;
    padding-top: 26px;
}

.shoplist-status div span:first-of-type {
    margin-right: 27px;
}

.shoplist-sort input {
    width: 22px;
    height: 22px;
    margin: 0;
    margin-right: 33px;
    float: left;
}

.shoplist-sort {
    font-size: 18px;
    padding: 30px 100px 30px 80px;
}

.shoplist-sort div {
    height: 22px;
    float: left;
}

.shoplist-sort ul {
    float: left;
}

.shoplist-sort:after {
    content: '';
    display: block;
    clear: both;
}

.shoplist-sort span {
    display: block;
    float: left;
    margin-right: 25px;
    margin-top: -2px;
}


