.background .container {
    height: 100%;
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: -600px;
}

.tag-img {
    position: absolute;
    right: 150px;
    top: 224px;
}

.bullet:after {
    content: '';
    display: block;
    clear: both;
}

.bullet {
    position: absolute;
    bottom: 100px;
    right: 141px;
}

.bullet li {
    width: 25px;
    height: 3px;
    margin: 0 11px;
    background-color: #aaa;
    float: left;
}

.bullet .current {
    background-color: #fff;
}


