.App {
    text-align: center;
}

html {
    margin: 0;
    padding: 0;
}

* {
    font-family: Helvetica, Tahoma, Arial, 'STXihei', "华文细黑", "Microsoft YaHei", "微软雅黑", SimSun, "宋体", Heiti, "黑体", sans-serif;
    letter-spacing: 1px;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s
}

p {
    font-family: Helvetica, Tahoma, Arial, 'STXihei', "华文细黑", "Microsoft YaHei", "黑体", SimSun, "宋体", Heiti, sans-serif;
}

a {
    color: #000;
    text-decoration: none;
}

li {
    list-style-type: none;
}

ul {
    padding-left: 0px;
}

dd {
    margin-left: 0px;
}

h1,
h2,
h3 {
    font-weight: 200;
    margin: 0;
}

hr {
    margin: 0;
}

footer {
    background-color: #363027;
    padding: 100px 0;
}

header {
    margin-bottom: -4px;
    position: relative;
    overflow: hidden;
}


