.container {
    width: 1200px;
    margin: 0 auto;
}

.culture {
    padding: 100px 0;
}

.cn-title-page2-culture {
    width: 548px;
}

.culture-column:after {
    content: '';
    display: block;
    clear: both;
}

.culture-column li {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    float: left;
    width: 50%;
}

.culture-column li:first-of-type,
.culture-column li:last-of-type {
    padding: 115px 0px 0px 158px;
}

.culture-column li:nth-child(2) {
    margin-bottom: -4px;
}

.culture-column li hr {
    width: 25px;
    border-color: #cc8c31;
    border-width: 2px;
    margin: 18px 0px 31px 0px;
}

.culture-column li h1 {
    font-size: 28px;
}

.culture-column li p {
    width: 283px;
    line-height: 24px;
    font-size: 14px;
}

.culture-column li a {
    display: block;
    width: 50px;
    padding: 15px 25px;
    background-color: #cc8c31;
    color: #fff;
    border-radius: 15px;
    font-size: 16px;
    margin-top: 30px;
}


