.goods {
    padding: 45px 0 37px 0;
    background-color: #f8f6f1;
    margin-bottom: 100px;
}

.goods-column {
    padding: 55px 100px 0 100px;
}

.goods-column:after {
    content: '';
    display: block;
    clear: both;
}

.goods-column div {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    float: left;
    text-align: center;
    width: 390px;
    height: 321px;
    margin: 0 55px;
    position: relative;
}

.goods-column h2 {
    font-size: 22px;
    margin-bottom: 17px;
}

.goods-column hr {
    width: 25px;
    border-color: #cc8c31;
    margin: 17px auto 29px auto
}

.goods-column p {
    text-indent: 2em;
    line-height: 24px;
}

.goods-column span {
    font-size: 14px;
    color: #333;
}

.goods-column a {
    display: block;
    position: absolute;
    left: 50%;
    bottom: 0px;
    margin-left: -48px;
    padding: 13px 22px;
    background-color: #cc8c51;
    color: #fff;
    border-radius: 12px;
    font-size: 16px;
}

.cn-title-page4-goods {
    width: 520px;
}


