.title {
    margin: 0 auto;
    text-align: center;
}

.en-title {
    color: #8d8a86;
    font-size: 14px;
}

.cn-title {
    padding: 20px 0px 52px 0px;
    width: 492px;
    margin: 0 auto;
}

.cn-title:after {
    content: '';
    display: block;
    clear: both;
}

.cn-title hr {
    width: 136px;
    border-color: #cd8a2f;
    margin: 13px 0;
    float: left;
}

.cn-title div {
    background-color: #cd8a2f;
    width: 4px;
    height: 4px;
    margin: 12px 10px;
    border-radius: 50%;
    float: left;
}

.cn-title h1 {
    color: #000;
    font-size: 27px;
    margin: 0px 28px;
    float: left;
}


