.container {
    width: 1200px;
    margin: 0 auto;
}

.column-nav {
    width: 100%;
}

.column-nav ul:after {
    content: '';
    display: block;
    clear: both;
}

.column-nav li {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 12.5%;
    padding: 30px 58px;
    float: left;
}

.column-nav ul {
    margin-top: 0;
}

.column-nav li:first-of-type {
    padding: 30px 49px;
}

.shop-column-nav-selected {
    background-color: #e9f0eb;
}

.shop-section01 {
    padding: 61px 140px 100px 140px;
}

.shop-section01:after {
    content: '';
    display: block;
    clear: both;
}

.shop-section01>div {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
    float: left;
    width: 285px;
    height: 417px;
    overflow: hidden;
    margin: 0 10px;
}

.hidden-cover {
    opacity: 0;
    position: absolute;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #10101077;
    text-align: center;
    padding-top: 200px;
}

.hidden-cover span {
    font-size: 18px;
    color: #fff;
}

.hidden-cover hr {
    width: 25px;
    margin: 10px auto 0 auto;
}

.hidden-cover:hover {
    opacity: 1;
}

.shop-section01-middle {
    padding-top: 57px;
    text-align: center
}

.shop-section01-middle h2 {
    font-size: 28px;
    color: #333;
    margin-bottom: 16px;
}

.shop-section01-middle hr {
    width: 25px;
    border-color: #cc8c53;
    margin: 0 auto 29px auto;
}

.shop-section01-middle p {
    color: #333;
    margin-bottom: 58px;
    line-height: 24px;
}

.shop-section02:after {
    content: '';
    display: block;
    clear: both;
}

.shop-section02 img {
    float: left;
    margin-right: -36px;
}

.shop-section02 div {
    float: left;
    width: 425px;
    padding: 34px 74px;
    background-color: #fff;
    margin-top: 50px;
}

.shop-section02 h1 {
    font-size: 28px;
}

.shop-section02 p {
    margin: 17px 0 28px 0;
}

.shop-section02 a {
    display: block;
    padding: 8px 25px;
    border: 1px solid #000;
    width: 84px;
}

.shop-section03 {
    padding: 60px 80px 160px 80px;
    text-align: center;
}

.shop-section03 h3 {
    font-size: 22px;
    margin-bottom: 20px;
}

.shop-section03 hr {
    width: 25px;
    border-width: 2px;
    border-color: #cc8c53;
    margin: 0 auto 30px auto;
}

.shop-section03-title p {
    line-height: 24px;
    font-size: 14px;
    margin-bottom: 85px;
}

.shop-section03-slideshow {
    position: relative;
}

.shop-section03-slideshow>div {
    position: absolute;
    top: 150px;
    ;
}

.shop-section03-slideshow>div:first-of-type {
    left: 0;
}

.shop-section03-slideshow>div:last-of-type {
    right: 0;
}

.shop-section03-column {
    padding: 0 52px;
}

.shop-section03-column:after {
    content: '';
    display: block;
    clear: both;
}

.shop-section03-column li {
    float: left;
    width: 292px;
    margin: 0 10px;
    padding-top: 30px;
    padding-bottom: 33px;
}

.shop-section03-column li:hover {
    -webkit-box-shadow: 3px 3px 6px 3px #66666677;
            box-shadow: 3px 3px 6px 3px #66666677
}

.shop-good-detail {
    float: left;
    text-align: left;
    padding-left: 55px;
}

.shop-good-detail p {
    font-size: 18px;
    color: #333;
    margin-bottom: 8px;
    margin-top: 38px;
}

.shop-good-detail span {
    color: #666;
}

.section3-like-icon {
    float: right;
    margin-top: 38px;
    margin-right: 57px;
}

.shop-section04-column {
    position: relative;
}

.shop-section04-column>div {
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 75px 104px;
    background-color: rgba(255, 255, 255, 0.5);
    margin-top: -126px;
    margin-left: -234px;
}

.shop-section04-column div h3 {
    font-size: 28px;
    color: #020202;
    margin-bottom: 27px;
}

.shop-section04-column a {
    display: block;
    padding: 12px 40px 12px 47px;
    width: 120px;
    border: 1px solid #000;
    margin: 0 auto;
}

.shop-section04-icon,
.shop-section05-icon {
    float: right
}

.shop-section05 {
    padding: 60px 111px 100px 111px;
    text-align: center;
}

.shop-section05 .shop-good-detail {
    padding-left: 16px;
}

.shop-section05 .shop-good-detail p {
    font-size: 14px;
    margin-top: 29px;
    margin-bottom: 12px;
}

.shop-section05 .shop-good-detail span {
    font-size: 12px;
}

.section05-column:after {
    content: '';
    display: block;
    clear: both;
}

.section05-column>div {
    float: left;
}

.shop-section05 h2 {
    font-size: 22px;
    color: #020202;
}

.shop-section05 hr {
    width: 25px;
    margin: 16px 0 42px 0;
}

.section05-column-list ul li {
    position: relative;
    float: left;
    width: 192px;
    padding: 15px 12px 29px 12px;
    margin: 0 3px;
}

.section05-column-list ul li:hover {
    -webkit-box-shadow: 3px 3px 6px 2px rgba(102, 102, 102, 0.5);
            box-shadow: 3px 3px 6px 2px rgba(102, 102, 102, 0.5)
}

.section05-column-list ul:after {
    content: '';
    display: block;
    clear: both;
}

.section05-column-title {
    width: 200px;
    text-align: left;
    margin-top: 120px;
    margin-right: 55px;
    cursor: pointer;
}

.section05-column-title hr {
    border-color: #cc8c53;
    border-width: 2px;
}

.section05-column-title p {
    font-size: 14px;
    line-height: 24px;
}

.section05-column-list {
    text-align: left;
}

.shop-section05-icon {
    margin-top: 29px;
    margin-right: 13px;
}

.shop-switch-left,
.shop-switch-right {
    position: absolute;
    top: 130px;
    height: 60px;
    overflow: hidden;
    background-color: #7c7c7c;
}

.shop-switch-left {
    width: 17px;
    padding-left: 3px;
    left: 0;
    margin-left: 12px;
}

.shop-switch-right {
    width: 20px;
    right: 0;
    margin-right: 12px;
}

.shop-switch-right svg {
    margin-left: -7px;
}

.section06-column:after {
    content: '';
    display: block;
    clear: both;
}

.section06-column>div {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
    float: left;
    width: 50%;
}

.section06-letter {
    position: absolute;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color: #10101070;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 101px 18px;
    opacity: 0;
    z-index: 10;
}

.section06-column>div:hover .section06-letter {
    opacity: 1;
}

.section06-letter h2 {
    text-align: center;
    color: #fff;
    font-size: 28px;
}

.section06-letter hr {
    width: 25px;
    margin: 18px auto 35px auto;
    border-color: #fff;
}

.section06-letter p {
    text-align: left;
    text-indent: 2em;
    width: 357px;
    margin: 0 auto;
    color: #fff;
    line-height: 24px;
    padding-left: 6px;
}

.section06-middle {
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -55px;
    margin-left: -7px;
    width: 15px;
    height: 110px;
    background-color: #ffcb2c;
    z-index: 15;
}

.section07-column {
    padding: 100px 50px 100px 150px;
    text-align: center;
}

.section07-column:after {
    content: '';
    display: block;
    clear: both;
}

.section07-column>div {
    float: left;
}

.section07-slideshow {
    position: relative;
    height: 362px;
    overflow: hidden;
}

.section07-slideshow div {
    position: absolute;
    top: 50%;
    margin-top: -30px;
    width: 20px;
    height: 60px;
    overflow: hidden;
}

.section07-slideshow .shop-switch-left {
    margin-left: 0;
    width: 17px;
}

.section07-slideshow .shop-switch-right {
    margin-right: 0;
}

.section07-VIP {
    padding-left: 26px;
}

.section07-VIP span {
    font-size: 14px;
}

.section07-VIP h2 {
    font-size: 28px;
    margin-top: 14px;
}

.section07-VIP>div {
    margin-bottom: 88px;
}

.section07-VIP ul li {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    float: left;
    width: 142px;
    margin: 0 36px;
    padding-bottom: 26px;
}

.section07-VIP ul li:nth-child(2) {
    padding-top: 19px;
}

.section07-VIP p {
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 20px;
}

.section07-VIP b {
    font-size: 14px;
}

.section07-VIP-describe li:first-of-type p {
    margin-top: 26px;
}

.section07-VIP-describe li:nth-child(2) p {
    margin-top: 29px;
}

.section07-VIP-describe li:last-of-type p {
    margin-top: 32px;
}

.section07-VIP-describe a {
    display: block;
    width: 52px;
    margin: 0 auto;
    font-size: 10px;
    padding: 3px 13px;
    border: 1px solid #000;
}

.section07-title {
    padding-bottom: 0;
}

.shop-section03 li,
.shop-section05 li {
    cursor: pointer;
}


