.advice-reply-dialog {
    position: fixed;
    top: 50%;
    left: 50%;
    margin-left: -300px;
    margin-top: -148px;
    width: 600px;
    -webkit-box-shadow: 0 0 0 10000px #00000048;
            box-shadow: 0 0 0 10000px #00000048;
    background-color: #fff;
    font-size: 18px;
    z-index: 999;
}

.advice-reply-dialog div:first-of-type a {
    float: right;
}

.advice-reply-dialog a {
    cursor: default;
}

.advice-reply-dialog div:first-of-type {
    padding: 10px 20px;
    text-align: left;
    clear: both;
    border-bottom: 1px solid #cc8c31
}

.advice-reply-dialog div:last-of-type {
    padding: 60px 0 80px 0;
    text-align: center;
}

.advice-reply-dialog p {
    margin-bottom: 30px;
}

.advice-reply-dialog div:last-of-type a {
    display: block;
    width: 50px;
    padding: 15px 25px;
    margin: 0 auto;
    background-color: #cc8c31;
    color: #fff;
    border-radius: 15px;
    font-size: 16px;
}


