.stuff .container {
    position: relative;
}

.stuff .title {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -218px;
    margin-top: -60.5px;
}

.stuff .four-letter-title {
    margin-left: -246px;
}

.stuff .title .en-title,
.cn-title-stuff-page1 h1,
.cn-title-stuff-page2 h1 {
    color: #fff;
}

.cn-title-stuff-page1 {
    width: 436px;
}

.cn-title-stuff-page1 hr,
.cn-title-stuff-page2 hr {
    border-color: #fff;
}

.cn-title-stuff-page1 div,
.cn-title-stuff-page2 div {
    background-color: #fff;
}

.stuff-describe {
    text-align: center;
    padding: 60px 0 50px 0;
}

.stuff-describe hr {
    width: 25px;
    border-color: #cc8c51;
    margin: 0 auto;
}

.stuff-describe h2 {
    margin-bottom: 15px;
    font-size: 22px;
    color: #000;
}

.stuff-describe p {
    line-height: 21px;
    color: #666;
    font-size: 14px;
}

.stuff-slideshow {
    padding-bottom: 45px;
    height: 401px;
    margin: 0 auto;
    overflow: hidden;
}

.stuff-slideshow:after {
    content: '';
    display: block;
    clear: both;
}

.stuff-slideshow li {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
    float: left;
    width: 670px;
    height: 401px;
    margin: 0 40px;
}

.stuff-slideshow li:first-of-type {
    margin-left: -134px;
}

.stuff-slideshow li:last-of-type {
    margin-right: -133px;
}

.stuff-slideshow li:nth-child(2n-1) div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(125, 125, 125, 0.7)
}

.stuff-describe-text {
    position: absolute;
    bottom: 0;
    width: 615px;
    background-color: rgba(0, 0, 0, 0.35);
    padding-left: 55px;
    padding-top: 9px;
    color: #fff;
}

.stuff-describe-text span {
    font-size: 15px;
}

.stuff-describe-text p {
    font-size: 13px;
}

.stuff-slideshow-bullet {
    width: 200px;
    margin: 0 auto 100px auto;
}

.stuff-slideshow-bullet li {
    float: left;
    width: 25px;
    height: 3px;
    background-color: #333;
    margin: 0 10px;
}

.stuff-slideshow-bullet .current {
    background-color: #cc8c51
}


