.aboutus {
    padding: 100px 82px;
}

.aboutus-container {
    width: 1036px;
    margin: 0 auto;
}

.aboutus>hr {
    width: 25px;
    border-width: 2px;
    border-color: #cc8c31;
    margin: 0 auto 40px auto;
}

.self-introduce {
    width: 506px;
    margin-left: 50px;
}

.information-aboutus div {
    float: left;
}

.information-aboutus:after {
    content: '';
    display: block;
    clear: both;
}

.self-introduce>span {
    display: block;
    font-size: 14px;
    color: #666;
    margin-bottom: 11px;
}

.aboutus-title {
    margin-bottom: 20px;
}

.aboutus-title:after {
    display: block;
    content: '';
    clear: both;
}

.aboutus-title h1 {
    float: left;
    font-size: 28px;
    color: #020202;
}

.aboutus-title>div,
.aboutus-title>hr {
    float: right;
}

.aboutus-title div {
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: #cc8c31;
    margin: 13px 0px 0px 63px
}

.aboutus-title hr {
    margin: 14px 0px 0px 12px;
    width: 300px;
    border-color: #cc8c31;
}

.self-introduce p {
    line-height: 24px;
    text-indent: 2em;
    color: #333;
}

.self-introduce a {
    display: block;
    width: 50px;
    padding: 15px 25px;
    background-color: #cc8c31;
    color: #fff;
    border-radius: 15px;
    font-size: 16px;
    margin-top: 30px;
}


