.contactus {
    text-align: center;
    padding: 100px 0;
}

.title p {
    line-height: 24px;
    color: #333;
    padding-bottom: 30px;
}

.map {
    height: 600px;
}

.feedback {
    padding: 100px 0
}

.advice-form {
    width: 1308px;
    margin: 0 auto;
}

.advice-form textarea {
    padding: 25px;
    width: 1148px;
    height: 298px;
    margin: 0px 54px 60px 54px;
    font-size: 20px;
}

.advice-form input {
    margin: 20px 54px;
    padding: 25px;
    width: 492px;
    font-size: 20px;
}

.advice-form button {
    display: block;
    background-color: #cc8c51;
    border: 0px;
    color: #fff;
    font-size: 18px;
    line-height: 50px;
    padding: 0px 36px;
    margin-top: 40px;
    margin-left: 54px;
}


