        .container {
            width: 1200px;
            margin: 0 auto;
        }
        
        .back-color {
            background-color: #f7f6f1
        }
        
        .position {
            padding: 60px 60px 80px 60px;
        }
        
        .position-slideshow:after {
            content: '';
            display: block;
            clear: both;
        }
        
        .position-slideshow div {
            float: left;
        }
        
        .position-column-list:after {
            content: '';
            display: block;
            clear: both;
        }
        
        .position-column-list {
            padding: 0px 13px;
        }
        
        .position-column-list li {
            float: left;
            width: 430px;
            padding: 0 25px;
            text-align: center;
        }
        
        .position-column-list h2 {
            font-size: 22px;
            color: #000;
            margin: 60px 0px 15px 0px;
        }
        
        .position-column-list h2:hover {
            color: #cc8c51;
        }
        
        .position-column-list p {
            text-indent: 2em;
            font-size: 17px;
            color: #333;
            text-align: left;
            padding: 0 28px;
            letter-spacing: 1px;
            line-height: 26px;
        }
        
        .cn-title-page4 {
            width: 548px;
        }
        
        .position-column-list hr {
            width: 25px;
            margin: 0 auto 28px auto;
            border-color: #cc8c51;
        }
        
        .position-column-list-warp {
            padding: 0 47px;
            position: relative;
        }
        
        .position-switch-left,
        .position-switch-right {
            position: absolute;
            top: 72px;
        }
        
        .position-switch-left {
            left: 15px;
        }
        
        .position-switch-right {
            right: 15px;
        }
        
        .product {
            padding: 100px 60px;
        }
        
        .product-series:after {
            content: '';
            display: block;
            clear: both;
        }
        
        .series-title {
            text-align: center;
        }
        
        .product-series {
            position: relative;
        }
        
        .product-series li {
            position: relative;
            right: 0;
            bottom: 0;
            width: 280px;
            height: 591px;
            float: left;
            margin: 0 21px;
            padding: 28px 18px 37px 18px;
            text-align: center;
        }
        
        .product-series li:hover {
            -webkit-box-shadow: 3px 3px 6px 3px rgba(102, 102, 102, 0.5);
                    box-shadow: 3px 3px 6px 3px rgba(102, 102, 102, 0.5);
            right: 3px;
            bottom: 3px;
        }
        
        .product-series li:hover hr {
            border-color: #cc8c51;
        }
        
        .product-series li:hover h2 {
            color: #cc8c51;
        }
        
        .product-series h2 {
            margin-top: 50px;
            margin-bottom: 10px;
        }
        
        .product-series span {
            font-size: 14px;
            color: #333;
        }
        
        .product-series hr {
            width: 25px;
            margin: 13px auto 33px auto;
        }
        
        .product-series p {
            padding: 0 15px;
            text-indent: 2em;
            text-align: left;
            line-height: 24px;
        }
        
        .product-series a {
            display: block;
            position: absolute;
            left: 50%;
            bottom: 37px;
            margin-left: -48px;
            padding: 13px 22px;
            background-color: #cc8c51;
            color: #fff;
            border-radius: 12px;
            font-size: 16px;
        }
        
        .cn-title-page1 {
            width: 446px;
        }
        
        .cn-title-page4 {
            width: 548px;
        }


